


[class*="Panel-"] {
  display: flex;
}

.Panel-1-Nav {
  flex-direction: column;
  height: 100vh;
  background-color: rgb(125, 224, 136);
  margin: 1%;
  border-radius: 8px;
  width: 30vw;
  font-size: 30px;
  box-shadow: 0 0 5px 2px rgba(0,0,0,.35);
}

.Panel-1-Nav > *:first-child {
  padding: 5% 10% 5% 10%;
}

.Panel-2-Nav-User {
  margin: 5%;
  width: 50%;
  border-radius: 8px;
  background-color: rgb(167, 167, 167);
  box-shadow: 0 0 5px 2px rgba(0,0,0,.35);
}

.Panel-2-Nav-Tile {
  margin: 5%;
  width: 100%;
  padding: 5%;
  border-radius: 8px;
  background-color: rgb(223, 223, 223);
  box-shadow: 0 0 5px 2px rgba(0,0,0,.35);
}

.Panel-0 {
  background-color: rgb(255, 255, 255);
  height: 100%;
  width: 100%;
}

ul {
  list-style-type: none;  /* Removes bullet points */
  padding-left: 0;         /* Removes the default padding */
}

.links-1 ul {
  list-style-type: none;  /* Removes bullet points */
  padding-left: 10%;         /* Removes the default padding */
}

.links-1 a {
	color: #18272F;
  position: relative;
  text-decoration: none;
}

.links-1 a::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  background-color: #18272F;
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}

.links-1 a:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}

.Panel-2-Nav {
  width: 100%;
  display: flex;
  flex-direction: column;
}


/* CSS */
.button-35 {
  margin: 5%;
  width: 90%;
  padding: 5%;
  align-items: center;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: transparent 0 0 0 3px,rgba(18, 18, 18, .1) 0 6px 20px;
  box-sizing: border-box;
  color: #121212;
  cursor: pointer;
  display: inline-flex;
  flex: column;
  font-family: Inter,sans-serif;
  font-size: 1.2rem;
  font-weight: 700;
  justify-content: center;
  line-height: 1;
  /*margin: 0;*/
  outline: none;
  /*padding: 1rem 1.2rem;*/
  text-align: center;
  text-decoration: none;
  transition: box-shadow .2s,-webkit-box-shadow .2s;
  white-space: nowrap;
  border: 0;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-35:hover {
  box-shadow: #121212 0 0 0 3px, transparent 0 0 0 0;
}
.emphasize-dark {
  box-shadow: 0 0 5px 2px rgba(0,0,0,.35);
}

.emphasize-light {
  box-shadow: 0 0 0 10px rgba(255,255,255,.25);
}

.emphasize-inset {
  box-shadow: inset 0 0 7px 4px rgba(255,255,255,.5);
}

.emphasize-border {
  box-shadow: inset 0 0 0 7px rgba(255,255,255,.5);
}